.rc-abstract {
  line-height:1.4;
}

.rc-abstract__title {
  padding:0;
  margin: 0;
}

.rc-abstract__content {
  padding: 0;
  margin: 0;
  overflow: hidden;
  position: relative;
}

.rc-abstract__expand-toggle-wrapper {
  position: absolute;
  bottom: 0;
  display: flex;
  left: 0;
  right: 0;
  align-items: flex-end;
  justify-content: center;
  height: 5rem;
  background: rgb(248,246,253);
  background: linear-gradient(0deg, rgba(248,246,253,1) 50%, rgba(248,246,253,0) 100%);
}

.rc-abstract__expander--expanded {
  transition: max-height 0.5s ease-in-out;
  max-height: 1000px;
  padding-bottom: 5rem;

}

.rc-abstract__expander--collapsed {
  transition: max-height .5s cubic-bezier(0, 1, 0, 1);
  max-height: 10rem;
}

.rc-abstract__content--open {
  max-height: 9000px;
}

.rc-abstract__expand-toggle {
  font-size: 0.85rem;
  border: solid 1px #7349f3;
  color: #7349f3;
  background: none;
  padding: 0.25rem 0.75rem;
  border-radius: 4px;
  cursor: pointer;
}

.rc-abstract__expand-toggle:focus,
.rc-abstract__expand-toggle:active {
  border-width: 3px;
  border-color: #7349f3;
  outline:none;
}