.rc-paper {
  background-color: #f8f6fd;
  padding: 1.5rem 2rem;
  margin-bottom: 1rem;
  box-shadow: -1px 4px 12px -4px #909090;
}

.rc-paper__title {
  margin: 0.5rem 0;
}

.rc-paper__author-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: block;
  color: lighten(#333, 15%);

  & a {
    color: lighten(#333, 15%);
  }
}

.rc-paper__author-list-item {
  display: inline-block;
}

.rc-paper__author-list-item:after {
  content: ', ';
  padding-right: 6px;
}

.rc-paper__author-list-item:nth-last-child(2):after {
  content: '';
}

.rc-paper__author-list-item:last-of-type:after {
  content: '.';
}

.rc-paper__author-list-item:last-of-type:before {
  content: ' & ';
  padding-right: 2px;
}

.rc-paper__corresponding-label {
  font-size: 0.85rem;
  font-style: italic;
  padding: 0.3rem 0;
  color: lighten(#333, 15%);
}

.rc-paper__info-header {
  display:flex;
  justify-content: space-between;
  padding-bottom: 0.85rem;
  margin-bottom:0.85rem;
  border-bottom: solid 1px #d2d1d4;

}

.rc-paper__info-header-left {
  display: flex;

    & > div {
      margin-right:1rem;
    }
}

.rc-paper__info-journal-link {
  padding: 0.25rem 0.5rem;
  background-color: #7349f3;
  color: #fff;
  text-decoration: none;
  border-radius:4px;
}

.rc-paper__info-doi {
  justify-self: flex-end;
}